input[type=text].jQKeyboard.focus,
textarea.jQKeyboard.focus{
    outline: 0;
    transition-property: border-color,box-shadow
}

div.jQKeyboardContainer{
    /* display: table; */
    display: flex;
    flex-direction: column;
    background: #000000;
    -webkit-box-shadow:0px 0px 4px #181818;
    box-shadow:0px 0px 4px #181818;
    border: 1px solid #5D5D5D;
    border-radius: 6px;
    padding: 4px;
    width: 100%;
    height: 300px;
    z-index: 3;
    margin: 5px auto 0;
}

div.jQKeyboardRow{
    /* display: table-row; */
    flex: 1;
    display: flex;
    justify-content: center;
    /* flex-wrap: wrap; */
    text-align: center;
}

button.jQKeyboardBtn{
    color: #ffffff;
    background: #424242;
    width: 55px;
    height: 55px;
    margin: 0;
    padding: 1px 6px 1px 6px;
    font-size: 18px;
    border: 2px solid #000000;
    border-radius: 6px;
    text-align: center;
/*    font-weight: bold;*/
    outline: none;
    cursor: pointer; 
}

button.jQKeyboardBtn:hover{
    background: #26b3f7;
}

button.jQKeyboardBtn:active,
button.jQKeyboardBtn.shift-active{
    background: #0972a5;
}

button.jQKeyboardBtn.disabled{
    color: #d9d9d9;
    background: #5e626e;
    background-image: -ms-linear-gradient(#939393, #939393);
    background-image: -webkit-linear-gradient(#939393, #939393);
    opacity: 0.9;
    filter: alpha(opacity=90);
}

